/**
 * vuex 
 */
let lifeData = uni.getStorageSync('lifeData') || {};

export default {
	//  namespaced: true 
	namespaced: true,
	/**
	 * vuex
	 */
	state: {
		/**
		 * 
		 * js
		 * () vk.getVuex('$user.userInfo');
		 *  vk.vuex.get('$user.userInfo');
		 * 
		 * {{ vk.getVuex('$user.userInfo') }}
		 * js
		 * vk.setVuex('$user.userInfo.avatar', avatar);
		 */
		userInfo: lifeData.$user.userInfo || {},
		permission:lifeData.$user.permission || [],
	},
	/**
	 * (state)state
	 */
	getters: {
		/**
		 * 
		 * js
		 * vk.vuex.getters('$user/getUserInfo');
		 * 
		 * that.$store.getters['$user/getUserInfo'];
		 */
		getUserInfo: (state) => {
			return state.userInfo;
		}
	},
	/**
	 * (action)
	 *  mutation   (type)    (handler)
	 *  state 
	 */
	mutations: {
		
	},
	/**
	 * mutation ==
	 * 1. Action  mutation 
	 * 2. Action 
	 */
	actions: {
		
	}
}