/**
 * url
 * @param {Object} data {a:1}
 * @returns {string}
 */
myfn.getParam = function(data) {
	let url = '';
	for (var k in data) {
		let value = data[k] !== undefined ? data[k] : '';
		url += `&${k}=${encodeURIComponent(value)}`
	}
	return url ? url.substring(1) : ''
}

/**
 * url
 * @param {string} url url
 * @param {Json} data json
 * @returns {string}
 */
myfn.getUrl = function(url, data) {
	//url?
	return url += (url.indexOf('?') < 0 ? '?' : '') + myfn.getParam(data)
}
/**
 * 
 */
myfn.getPageUrl = function() {
	let routes = getCurrentPages(); // 
	let curRoute = routes[routes.length - 1].route //
	let curParam = routes[routes.length - 1].options; //
	// 




	return myfn.getUrl(curRoute, curParam)

}
/**
 * @param {string} url 
 * @returns {Promise} 
 */
myfn.downLoadImg = function(url) {
	return new Promise(() => {

		uni.authorize({
			scope: 'scope.writePhotosAlbum',
			success() {
				uni.downloadFile({
					url: url,
					success: (res) => {
						if (res.statusCode === 200) {
							uni.saveImageToPhotosAlbum({
								filePath: res.tempFilePath,
								success: function() {
									console.log('success')
									uni.showToast({
										title: "",
										icon: "none"
									});
								},
								fail: function(error) {
									console.log(error)
									uni.showToast({
										title: "",
										icon: "none"
									});
								}
							});
						} else {
							uni.showToast({
								title: "",
								icon: "none",
							});
						}
					},
					fail(err) {
						uni.showToast({
							title: "",
							icon: "none"
						});
					}
				});
			},
			fail(err) {
				uni.showToast({
					title: "-",
					icon: "none"
				});
			}
		})












	});
}
