/**
 */
myfn.cropImg = function(file) {
	return new Promise((resolve, reject) => {
		let ext
		let filePathProcessed = file.path // 









		uni.getImageInfo({
			src: file.path,
			success(info) {
				ext = info.type.toLowerCase()
				resolve({
					path: filePathProcessed,
					ext,
					fileType: file.fileType
				})
			},
			fail(err) {
				reject(new Error(err.errMsg || ''))
			}
		})

	})
}
export default myfn;
