var counterNum = 0;
/**
 * request 
 * : 
 * @param {String} url                
 * @param {Object/String/ArrayBuffer} data                                       AppArrayBuffer
 * @param {Object} header              headerheader  Referer         AppH5cookieH5
 * @param {String} method              POST  GET
 * @param {Number} timeout             ms 3000(30)
 * @param {String} dataType           json  json JSON.parse
 * @param {String} responseType        text textarraybuffer App
 * @param {Boolean} sslVerify          true ssl AppHBuilderX 2.3.3+          AppHBuilderX 2.3.3+
 * @param {Boolean} withCredentials    false cookies            H5HBuilderX 2.6.15+
 * @param {Boolean} firstIpv4          false DNSipv4                        App-Android  (HBuilderX 2.8.0+)
 * @param {String} success            
 * @param {String} fail               
 * @param {String} complete           
 * 
 * @param {String} errorCodeName      
 * @param {String} errorMsgName       errorCodeName0alert
 * @param {Boolean} needAlert         alert
 * 
 * 
vk.request({
	url: `https://www.xxx.com/api/xxxx`,
	method:"POST",
	header:{
		"content-type": "application/x-www-form-urlencoded",
	},
	data,
	success: function(data){

	},
	fail: function(err){

	}
});
 */

var request = function(obj = {}) {
	let vk = uni.vk;
	let config = vk.callFunctionUtil.getConfig();
	if (!obj.method) obj.method = "POST"; // POST
	if (typeof obj.dataType === "undefined") obj.dataType = "json";
	if (obj.dataType == "default" || obj.dataType === "") delete obj.dataType;
	if (typeof obj.header === "undefined" && typeof obj.headers !== "undefined") {
		obj.header = obj.headers;
	}
	if (typeof obj.timeout === "undefined") obj.timeout = 30000; //  ms(30)
	let Logger = {};
	if (config.debug) {
		Logger.params = (typeof obj.data == "object") ? vk.pubfn.copyObject(obj.data) : obj.data;
		Logger.startTime = new Date().getTime();
		let url = obj.url;
		let path = obj.url.split("?")[0];
		path = path.substring(path.indexOf("://") + 3);
		Logger.domainName = path.substring(0, path.indexOf("/"));
		Logger.action = path.substring(path.indexOf("/") + 1);
		Logger.url = url;
	}
	if (obj.title) vk.showLoading(obj.title);
	let promiseAction = new Promise(function(resolve, reject) {
		uni.request({
			...obj,
			success: function(res) {
				requestSuccess({
					res,
					params: obj,
					Logger,
					resolve,
					reject
				});
			},
			fail: function(res) {
				requestFail({
					res,
					params: obj,
					Logger,
					reject
				});
			},
			complete: function(res) {
				requestComplete({
					res,
					params: obj,
					Logger
				});
			}
		});
	});
	promiseAction.catch(error => {});
	return promiseAction;
}
// 
function requestSuccess(obj = {}) {
	let vk = uni.vk;
	let config = vk.callFunctionUtil.getConfig();
	let {
		res = {},
		params,
		Logger,
		resolve,
		reject
	} = obj;
	let {
		title,
		needOriginalRes,
		dataType,
		errorCodeName,
		errorMsgName,
		success
	} = params;
	let data = res.data || {};
	if (vk.pubfn.isNotNullAll(errorCodeName, data[errorCodeName])) {
		data.code = data[errorCodeName];
		delete data[errorCodeName];
	}
	if (vk.pubfn.isNotNullAll(errorMsgName, data[errorMsgName])) {
		data.msg = data[errorMsgName];
		delete data[errorMsgName];
	}
	if (res.statusCode >= 400 || data.code) {
		requestFail({
			res: data,
			params,
			Logger,
			reject
		});
	} else {
		if (title) vk.hideLoading();
		if (needOriginalRes) data.originalRes = vk.pubfn.copyObject(res);
		if (config.debug) Logger.result = (typeof data == "object") ? vk.pubfn.copyObject(data) : data;
		if (typeof success === "function") success(data);
		if (typeof resolve === "function") resolve(data);
	}
}

// 
function requestFail(obj = {}) {
	let vk = uni.vk;
	let config = vk.callFunctionUtil.getConfig();
	let {
		res = {},
		params,
		Logger,
		reject
	} = obj;
	let {
		title,
		needAlert,
		fail
	} = params;
	if (typeof needAlert === "undefined"){
		needAlert = (typeof fail === "function") ? false : true;
	}
	
	let errMsg = "";
	let sysErr = false;
	if (typeof res.code !== "undefined") {
		errMsg = res.msg;
	} else {
		sysErr = true;
		errMsg = JSON.stringify(res);
	}
	if (errMsg.indexOf("fail timeout") > -1) {
		sysErr = true;
		errMsg = "";
	}
	if (needAlert && vk.pubfn.isNotNull(errMsg)) {
		if (sysErr) {
			vk.toast("", "none");
		} else {
			vk.alert(errMsg);
		}
	}
	if (title) vk.hideLoading();
	if (config.debug) Logger.error = res;
	if (typeof fail === "function") fail(res);
	if (typeof reject === "function") reject(res);
}

// 
function requestComplete(obj = {}) {
	let vk = uni.vk;
	let config = vk.callFunctionUtil.getConfig();
	let {
		res = {},
		params,
		Logger
	} = obj;
	let {
		title,
		needOriginalRes,
		complete
	} = params;
	if (config.debug) {
		Logger.endTime = new Date().getTime();
		Logger.runTime = (Logger.endTime - Logger.startTime);
		let colorArr = config.logger.colorArr;
		let colorStr = colorArr[counterNum % colorArr.length];
		counterNum++;
		console.log("%c--------" + Logger.action + "--------", 'color: ' + colorStr +';font-size: 12px;font-weight: bold;');
		console.log(": ", Logger.url);
		console.log(": ", Logger.params);
		console.log(": ", Logger.result);
		console.log(": ", res.statusCode, "http");
		console.log(": ", Logger.runTime, "");
		console.log(": ", vk.pubfn.timeFormat(Logger.startTime, "yyyy-MM-dd hh:mm:ss"));
		if (Logger.error) {
			console.error("Error: ", Logger.error);
			if (Logger.error.err && Logger.error.err.stack) {
				console.error("Stack: ", Logger.error.err.stack);
			}
		}
		console.log("%c--------" + Logger.action + "--------", 'color: ' + colorStr +';font-size: 12px;font-weight: bold;');
	}
	let data = res.data;
	if (needOriginalRes) data.originalRes = vk.pubfn.copyObject(res);
	if (typeof complete === "function") complete(data);
}

export default request;
