/**
 * 11
 * @description  : 1
 * @params {String || Number} 	startTime	 
 * vk.pubfn.dateDiff(startTime);
 */
pubfn.dateDiff = function(startTime) {
	if (!startTime){
		return "";
	}
	if(typeof startTime == "number"){
		if (startTime.toString().length == 10) startTime *= 1000;
		startTime = new Date(startTime);
		startTime = pubfn.getFullTime(startTime);
	}if(typeof startTime == "string"){
		startTime = startTime.replace("T", " ");
		startTime = startTime;
		startTime = new Date(startTime.replace(/-/g, "/"));//-/new Date
	}
	var endTime = new Date();//
	var nd = 1000 * 24 * 60 * 60;//
	var nh = 1000 * 60 * 60;//
	var nm = 1000 * 60;//
	var ns = 1000;//long diff;try {
	var diff = endTime.getTime() - startTime.getTime();
	var day = Math.floor(diff / nd);//
	var hour = Math.floor(diff % nd / nh);//
	var min = Math.floor(diff % nd % nh / nm);//
	var sec = Math.round(diff % nd % nh % nm / ns);////
	var showStr = "1 ";
	if (day > 0) {
		showStr = day + "";
	} else if (hour > 0) {
		showStr = hour + "";
	} else if (min > 0) {
		showStr = min + "";
	} else if (sec > 0) {
		showStr = sec + "";
	}
	return showStr;
}
/**
 * 11
 * @description   : 3 
 * @params {String || Number} endTime	 
 * vk.pubfn.dateDiff2(endTime);
 */
pubfn.dateDiff2 = function(startTime) {
	if (!startTime){
		return "";
	}
	if(typeof startTime == "number"){
		if (startTime.toString().length == 10) startTime *= 1000;
		startTime = new Date(startTime);
		startTime = pubfn.getFullTime(startTime);
	}if(typeof startTime == "string"){
		startTime = startTime.replace("T", " ");
		startTime = startTime;
		startTime = new Date(startTime.replace(/-/g, "/"));//-/new Date
	}
	var endTime = new Date();//
	var nd = 1000 * 24 * 60 * 60;//
	var nh = 1000 * 60 * 60;//
	var nm = 1000 * 60;//
	var ns = 1000;//long diff;try {
	var diff = startTime.getTime() - endTime.getTime();
	var day = Math.floor(diff / nd);
	var hour = Math.floor(diff % nd / nh);
	var min = Math.floor(diff % nd % nh / nm);
	var sec = Math.round(diff % nd % nh % nm / ns);
	var showStr = "1";
	if (day > 0) {
		showStr = day + "";
	} else if (hour > 0) {
		showStr = hour + "";
	} else if (min > 0) {
		showStr = min + "";
	} else if (sec > 0) {
		showStr = sec + "";
	}
	return showStr;
};
/**
 * 
 * @description 
 * :
 * 3210 -> 3
 * 31210 -> 3
 * 1523412 -> 1
 * 15234120 ->1
 * @params {Number} n 
 * vk.pubfn.numStr(n);
 */
pubfn.numStr = function (n){
	if(typeof n == "string"){
		n = parseFloat(n);
	}
	var str = n;
	if(n < 1000){
		str = n;
	}else if(n < 10000){
		var n1 = Math.floor(n/100);
		str = n1/10+""
	}else if(n < 1000000){
		var n1 = Math.floor(n/1000);
		str = n1/10+""
	}else if(n < 10000000){
		var n1 = Math.floor(n/1000000);
		str = n1+""
	}else if(n < 100000000){
		var n1 = Math.floor(n/10000000);
		str = n1+""
	}else if(n >= 100000000){
		var n1 = Math.floor(n/10000000);
		str = n1/10+""
	}
	return str;
};
